<template>
  <v-card>
    <v-card-title>
      Nutrition
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="search"
    >
      <template slot="items" slot-scope="props">
        <td>{{ props.item.value }}</td>
        <td class="text-xs-center">{{ props.item.text }}</td>
        <td class="text-xs-center">{{ props.item.fat }}</td>
        <td class="text-xs-center">{{ props.item.carbs }}</td>
        <td class="text-xs-center">{{ props.item.protein }}</td>
        <td class="text-xs-center">{{ props.item.iron }}</td>
      </template>
      <v-alert slot="no-results" :value="true" color="error" icon="warning">
        Your search for "{{ search }}" found no results.
      </v-alert>
    </v-data-table>
  </v-card>
</template>

<script>
import Error from '@/services/ErrorService'
  export default {
    data: () => ({
        search: '',
        headers: [
          {
            text: 'Error ID',
            align: 'left',
            sortable: true,
            value: 'values'
          },
            { text: 'Calories', align: 'center', value: 'cal' },
            { text: 'Fat (g)', align: 'center', value: 'fat' },
            { text: 'Carbs (g)', align: 'center', value: 'carbs' },
            { text: 'Protein (g)', align: 'center', value: 'protein' },
            { text: 'Iron (%)', align: 'center', value: 'iron' }
        ],
      }),
        created () {
        Error.getAll()
        .then(function (res, err) {
          this.desserts = []
          var i
          for (i = 0; i < res.data.length; i++) {
            this.desserts.push({
              value: res.data[i].id,
              text: res.data[i].generalComment
            })
          }
        }.bind(this))
        }


        // desserts: [
